}
.App
{
  color: #000000;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #000000;
}
h1{
-webkit-user-select: none;
-khtml-user-select: none;
-moz-user-select: none;
-ms-user-select: none;
-o-user-select: none;
user-select: none;
  font-size: 80px;
  margin: 0px;
  color: #e6e6e6;
}
.fancyText
{
-webkit-user-select: none;
-khtml-user-select: none;
-moz-user-select: none;
-ms-user-select: none;
-o-user-select: none;
user-select: none;
  color: #e6e6e6;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
 font-size: 20px;
}
